* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

.container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    justify-content: center;
    justify-content: space-between;
}
.card {
    background-color: white;
    width: 350px;
    height: 530px;
    margin: 40px;
    overflow: hidden;
    box-shadow: 0px 0px 15px 5px;
    transition: 0,5s;
    animation: ease-in;
    border-radius: 15px;
    border: 3px solid black;
}

.card:hover {
    transform: scale(1.1);
    box-shadow: 0px 0px 15px 0px;
}

.image-container img{
    width: 348px;
    height: 225px;
    margin-bottom: 30px;
}


h1 {
    padding-top: 30px;
    text-align: center;
    align-items:  center;
}

.Card-Group{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    justify-content: space-between;
    height: 1000px;
    width: 75%;
    margin-left: 200px;
}

.card-container {
    border-radius: 10px;
    width: 300px;
    height: 425px;
    overflow: hidden;
    box-shadow: 0px 0px 15px 5px;
    transition: 0,5s;
    animation: ease-in;
    margin: 75px 60px;
    border: 3px solid black;
}

.image-container img{
    width: 348px;
    height: 225px;
    margin-bottom: 30px;
}

.card-container:hover{
    transform: scale(1.1);
    box-shadow: 0px 0px 15px 0px;
}



.card-content {
    margin: 1rem;
    margin-left: 1.5rem;
    text-decoration: none;
}

.card-title {
    margin-bottom: 0.5rem;
}

.card-text {
    width: 1oopx;
}

.card-body {
    text-align: center;
    text-decoration: none;
    
}




